import React, { useState, useEffect, useCallback, useMemo } from 'react';
import { useHistory } from 'react-router-dom';
import styles from './profile.module.css';
import { getLogout, getUser, getRefreshUser } from '../redux/actions/auth';
import { PasswordInput, EmailInput, Button, Input} from '@ya.praktikum/react-developer-burger-ui-components';
import { useAppDispatch, useAppSelector } from '../redux/hooks';

declare module 'react' {
  interface FunctionComponent<P = {}> {
    (props: PropsWithChildren<P>, context?: any): ReactElement<any, any> | null;
  }
}

export const  ProfilePage: React.FC = () => {
  const history = useHistory(); 
  const dispatch = useAppDispatch();
  const {
    user, 
    userRequest,
    userFailed,
  } = useAppSelector((store) => store.auth);

  const [name, setName] = useState('');
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [current, setCurrent] = useState('');

  useEffect(() => {
    dispatch(getUser());
  }, [dispatch]);

useEffect(()=>{
  setName(user === null ? '' : user.user.name);
  setEmail(user === null ? '' : user.user.email);
}, [user]);

  const onSave =  useCallback(
    (e: React.FormEvent) => {
      e.preventDefault();
      dispatch(getRefreshUser({ 
        'email': email,
        'password': password }));
    }, [email, password]
  );

  const onCancel = useCallback(
    (e: React.FormEvent) => {
      e.preventDefault();
      setName(user === null ? '' : user.user.name);
      setEmail(user === null ? '' : user.user.email);
      setPassword('');
    }, [email, password, name]
  );

  const profileOnClick = useCallback(
    () => {
      setCurrent('');
      history.replace({ pathname: '/profile' });
    },
    [history]
  );

  const orderOnClick = useCallback(
    () => {
      setCurrent(' ');
      history.replace({ pathname: '/profile/orders' });
    },
    [history]
  );

  const outOnClick = useCallback(
    () => {
      setCurrent(' ');
      dispatch(getLogout());
      history.replace({ pathname: '/login' });
    },
    [history]
  );


const userStatus = useMemo(
  () => {
    return userFailed ? (
      <p className={styles.text}>    </p>
    ) : userRequest ? (
      <p className={styles.text}>  ...</p>
    ) : user ? (
     ''
    ) : (
      <p className={styles.text}>    </p>
    );
  },
  [userRequest, user, userFailed]
  );

  return (

    <div className={styles['profile-container']}>
      {userStatus}
      <div className={styles['top-container']}>
        <div className={styles['input-container']}>
          <span 
            className={current === '' ? 
              'text text_type_main-default' 
               : 
              'text text_type_main-default text_color_inactive'
              } 
              onClick={profileOnClick}
           >
              
           </span>
          <Input 
            type={'text'}
            value={name}
            placeholder={''}
            onChange={e => setName(e.target.value)}
            icon={'EditIcon'}
            size={'small'}
          />
        </div>
        <div className={styles['input-container']}>
          <span 
            className={current === ' ' ? 
              'text text_type_main-default' 
               : 
              'text text_type_main-default text_color_inactive'
              } 
              onClick={orderOnClick}
           >
               
           </span>
          <EmailInput
            value={email}
            name={''}
            onChange={e => setEmail(e.target.value)}
            size={'small'}
          />
        </div>
        <div className={styles['input-container']}>
          <span 
            className={current === '' ? 
              'text text_type_main-default' 
               : 
              'text text_type_main-default text_color_inactive'
              } 
              onClick={outOnClick}
           >
              
           </span>
          <PasswordInput 
            value={password}
            name={''}
            onChange={e => setPassword(e.target.value)}
            size={'small'}
          />
        </div>
        <div className={styles['input-container']}>
        <Button onClick={onSave}>
            
          </Button>
          <Button onClick={onCancel}>
            
          </Button>
        </div>
      </div>
      <div className={styles['bottom-container']}>
        <div className={styles['input-container']}>
          <span className={'text text_type_main-small text_color_inactive'}>
                
          </span>
        </div>
        <div className={styles['input-container']}>
          <span className={'text text_type_main-small text_color_inactive'}>
               
          </span>
        </div>
      </div>
    </div>
  )
}
